import { Line } from 'react-chartjs-2'

export const LineChart = ({ labels, data, label }) => {
    return (<>
        <Line
            data={{
                labels: labels,
                datasets: [{
                    label: label,
                    data: data,
                    fill: 'origin',      // 0: fill to 'origin',
                    backgroundColor: [
                        'rgba(0, 0, 0, 0.2)',
                    ],
                    borderColor: [
                        'rgba(0, 0, 0, 1)',
                    ],
                    borderWidth: 1
                }]
            }}
        />
    </>)
}