import { createSlice } from '@reduxjs/toolkit'

export const currenciesSlice = createSlice({
    name: 'currencies',
    initialState: {
        data: []
    },
    reducers: {
        setData: (state, action) => {
            state.data.push(action.payload)
        },
        setStatistics: (state, action) => {
            state.data.map(currency => (currency.Cur_ID === action.payload[0].Cur_ID) ? currency.Cur_Statistics = action.payload : currency)
        }
    }
})

export const { setData, setStatistics } = currenciesSlice.actions

export default currenciesSlice.reducer