import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { setStatistics } from "../../features/currencies/currenciesSlice";
import { LineChart } from "../lineChart/LineChart";
import { Avatar, Card, CardHeader, CardMedia, Collapse, Grid, IconButton } from "@mui/material"
import { styled } from "@mui/material/styles";
import {  ExpandMore} from "@material-ui/icons"


const ExpandChart = styled((props) => {
    const { expand, ...other } = props
    return <IconButton {...other} />
})(({ theme, expand }) => ({
    transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
    }),
}))



export const Currency = ({ currency, startDate, endDate }) => {
    const dispatch = useDispatch()
    const [isStatLoaded, setIsStatLoaded] = useState(false)
    const chartData = {
        labels: [],
        data: []
    }

    // const [didGrow, setDidGrow] = useState(false)
    // const checkDidGrow = () => {
    //     const currentRate = chartData.data[chartData.data.length-1]
    //     const prevRate = chartData.data[chartData.data.length - 2]
    //     if (currentRate>prevRate) setDidGrow(true)
    // }
    const [expanded, setExpanded] = useState(false)
    const handleExpandClick = () => {
        setExpanded(!expanded)
    }

    useEffect(() => {
        if (!currency.Cur_Statistics) {
            fetch(`https://www.nbrb.by/API/ExRates/Rates/Dynamics/${currency.Cur_ID}?startDate=${startDate}&endDate=${endDate}`)
            .then(responseStat => responseStat.json())
            .then(dataStat => {
                dispatch(setStatistics(dataStat))
                setIsStatLoaded(true)
            })}
        
    }, [dispatch, startDate, endDate, currency.Cur_ID])



    if (isStatLoaded && chartData.data.length===0) {
        currency.Cur_Statistics.map(day => {
            chartData.labels.push(day.Date.slice(5, 10))
            return chartData.data.push(day.Cur_OfficialRate)
        })
    }

    return (
        <>
            <Grid item>
                <Card>
                    <CardHeader
                        avatar={
                            <Avatar>{currency.Cur_Abbreviation}</Avatar>
                        }
                        title={`Валюта: ${currency.Cur_Name}`}
                        subheader={`Текущий курс: ${currency.Cur_OfficialRate}`} 
                        action={
                            <ExpandChart
                                expand={expanded}
                                onClick={handleExpandClick}
                                aria-expanded={expanded}
                                aria-label="show more"
                            >
                                <ExpandMore />
                            </ExpandChart>
                        }
                    />
                    <Collapse in={expanded} timeout="auto" unmountOnExit>
                        <CardMedia>
                            <LineChart
                                labels={currency.Cur_Statistics && currency.Cur_Statistics.map(day=> day.Date.slice(5,10))}
                                data={currency.Cur_Statistics && currency.Cur_Statistics.map(day=> day.Cur_OfficialRate)}
                                label={currency.Cur_Abbreviation} />
                        </CardMedia>
                    </Collapse>
                </Card>
            </Grid>

        </>
    )
}