import { Button, ClickAwayListener, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, Grid, InputLabel, ListItem, MenuItem, Select, TextField } from "@material-ui/core";
import { Add, AddCircleOutline } from "@material-ui/icons";
import { Box } from "@material-ui/system";
import { Fab, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";

function AddSavingsDialog({ onClose, open, savingObj }) {
   const [selectValue, setSelectValue] = useState('')
   const handleChange = (event) => {
      setSelectValue(event.target.value)
   }
   return (
      <>
         <Dialog open={open}>
            <DialogTitle>Add Savings</DialogTitle>
            <DialogContent >
               <FormControl xs={{ color: 'inherit' }} >
                  <TextField
                     margin='normal'
                     label='Amount'
                     variant='standard'
                  />
               </FormControl>
               <FormControl margin='normal'>
                  <InputLabel id="demo-simple-select-label">Currency</InputLabel>
                  <Select
                     labelId="currency-select-label"
                     id="currency-select"
                     value={selectValue}
                     label="Currency"
                     onChange={handleChange}
                     variant='standard'
                  >
                     <MenuItem value='USD'>$</MenuItem>
                     <MenuItem value='GBP'>£</MenuItem>
                     <MenuItem value='RUB'>₽</MenuItem>
                     <MenuItem value='EUR'>€</MenuItem>
                     <MenuItem value='TRY'>₤</MenuItem>
                     <MenuItem value='BTC'>₿</MenuItem>
                  </Select>
               </FormControl>
            </DialogContent>
            <DialogActions>
               <Button variant='contained'>Confirm</Button>
               <Button onClick={() => onClose()}>Cansel</Button>
            </DialogActions>
         </Dialog>
      </>
   );
}

export default function Savings() {
   const [open, setOpen] = useState(false)

   const handleClickOpen = () => {
      setOpen(true)
   }
   const handleClose = () => {
      setOpen(false)
   }

   return (
      <>
         <Typography variant="h4" component="div">
            Coming soon! Monitor your savings here.
         </Typography>
         <AddSavingsDialog onClose={handleClose} open={open} />
         <Fab
            sx={{ margin: "auto 10px 10px auto" }}
            color="primary"
            aria-label="add"
            onClick={() => handleClickOpen()}
         >
            <Add />
         </Fab>
      </>
   );
}
