import React from 'react';
// import { Router } from 'react-router';
import { CurrencyList } from './components/currencyList/currencyList';
import { BottomNavigationComponent } from './components/bottomNavigation/BottomNavigation';
import { Container, useMediaQuery, createTheme, ThemeProvider, CssBaseline, Button } from '@mui/material';
import './App.css';
import TopBar from './components/appBar/AppBar';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link, useLocation, useRouteMatch
} from "react-router-dom";
import Converter from './components/converter/Converter';
import Savings from './components/savings/Savings';



function App() {
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)')
  const theme = React.useMemo(
    () =>
      createTheme({
        palette: {
          mode: prefersDarkMode ? 'dark' : 'light',
        },
      }),
    [prefersDarkMode],
  );
  return (
    <Router>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Container maxWidth="sm" sx={{padding:'7vh 1rem', position: 'relative'}}>
          <TopBar />
          <Switch>
            <Route path='/currency-MUI-pages/savings'>
              <Savings/>
            </Route>
            <Route path='/currency-MUI-pages/converter'>
              <Converter/>
            </Route>
            <Route path="/">
              <CurrencyList />
            </Route>
            </Switch>
          <BottomNavigationComponent />
        </Container>
      </ThemeProvider>
    </Router>

  );
}

export default App;


