import React from "react"
import { BottomNavigation, BottomNavigationAction, Icon } from "@mui/material"
import { Box } from "@mui/system"
import { useState } from "react"
import { BrowserRouter, Link, Switch,Route, NavLink} from "react-router-dom"
import { CurrencyList } from "../currencyList/currencyList"
import TopBar from '../appBar/AppBar'
    
export const BottomNavigationComponent = () => {
    const [value, setValue] = useState('rates')
    const handleChange = (event, newValue) => {
        setValue(newValue)
    }
    return (
        <>
            <Box sx={{ position: "fixed", bottom: 0, left: 0, width: "100%" }}>
                <BottomNavigation value={value} onChange={handleChange} >
                    <BottomNavigationAction
                        label="Rates"
                        value="rates"
                        icon={<Icon>trending_up</Icon>}
                        component={NavLink}
                        to='/currency-MUI-pages'
                    />
                    <BottomNavigationAction
                        label="Savings"
                        value="savings"
                        icon={<Icon>savings</Icon>}
                        component={NavLink}
                        to='/currency-MUI-pages/savings'
                    />
                    <BottomNavigationAction
                        label="Converter"
                        value="converter"
                        icon={<Icon>loop</Icon>}
                        component={NavLink}
                        to='/currency-MUI-pages/converter'
                    />
                </BottomNavigation>
            </Box>
        </>
    )
}