import {  Grid } from '@mui/material';
import { Box } from '@mui/system';
import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { setData } from '../../features/currencies/currenciesSlice';
import { Currency } from '../currency/Currency';

const currIDList = [
    451,      //? Евро
    431,      //? Доллар США
    456,      //? Российский рубль
    // 468,      //? Тайский бат
    460,      //? Турецкая лира
    426,      //? Швейцарский франк
    429,      //? Фунт стерлингов
    449,      //? Гривна
    // 'https://blockchain.info/ticker',                 //? Bitcoin
];

export const CurrencyList = () => {
    const [dates, setDates] = useState([])
    const data = useSelector(state => state.currencies.data)
    const dispatch = useDispatch()

    const getDates = (spread) => {
        const today = new Date()
        const firstStatDay = new Date()
        firstStatDay.setDate(today.getDate() - spread)
        return [`${firstStatDay.toJSON().slice(0, 10)}`, `${today.toJSON().slice(0, 10)}`]
    }

    useEffect(() => {
        document.title='Exchange rates'
        if (data.length===0) {
            setDates(getDates(40))
            currIDList.map(async (Cur_ID) => {
                fetch(`https://www.nbrb.by/api/exrates/rates/${Cur_ID}`)
                .then(responseCur => responseCur.json())
                .then(dataCur => dispatch(setData(dataCur)))
                
            })
        }
        
    }, [dispatch])


    return (
        <Box sx={{padding: '1.5vh 0'}}>
        <Grid container spacing={1.5} direction="column" >
            {data.map(currency => <Currency
                currency={currency}
                startDate={dates[0]}
                endDate={dates[1]}
                key={currency.Cur_ID}
            />)}
        </Grid>
</Box>
    )
}
